@import url('https://fonts.googleapis.com/css2?family=Noticia+Text:ital,wght@0,400;0,700;1,400&display=swap');

.noticia-font {
    font-family: 'Noticia Text', serif;
}

.font-w700 {
    font-weight: 700;
}

.header {
    border-bottom: 1px solid #eee;
    padding: 1rem 2rem;
}

.header__heading {
    display: block;
    text-align: center;
}

.nav-scroller {
    position: relative;
    z-index: 2;
    height: 2.75rem;
    overflow-x: hidden;
}

.nav-item {
    line-height: 3.3;
    color: #6c757d;
}

.nav-item:hover:nth-of-type(1) {
    border-bottom: 2px solid #cc0000;
}

.nav-item:hover:nth-of-type(1) {
    color: #cc0000;
}
.nav-item:hover:nth-of-type(2) {
    border-bottom: 2px solid #28a745;
}

.nav-item:hover:nth-of-type(2) {
    color: #28a745;
}

.nav-item:hover:nth-of-type(3) {
    border-bottom: 2px solid #007bff;
}

.nav-item:hover:nth-of-type(3) {
    color: #007bff;
}

.nav-item:hover:nth-of-type(4) {
    border-bottom: 2px solid #17a2b8;
}

.nav-item:hover:nth-of-type(4) {
    color: #17a2b8;
}

.nav-item:hover:nth-of-type(5) {
    border-bottom: 2px solid #ffc107;
}

.nav-item:hover:nth-of-type(5) {
    color: #ffc107;
}

.nav-item:hover:nth-of-type(6) {
    border-bottom: 2px solid #6c757d;
}

.nav-item:hover:nth-of-type(6) {
    color: #6c757d;
}

.nav-item--active {
    color: #cc0000;
    border-bottom: 2px solid #cc0000;
}

.h-md {
    height: 80px;
}

.h-lg {
    height: 240px;
}

.sub-img {
    width: 250px;
}

.search-overlay {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 9000;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.8);
}

.live-search {
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 5px;
}

.live-search-field {
    background-color: transparent;
    border-radius: 5px;
    border: none;
    padding: 1rem 2rem;
    width: 100%;
    font-size: 2rem;
    outline: none;
    flex: 1;
}

.live-search-icon {
    font-size: 3rem;
    margin-right: 2rem;
}

.live-search-close {
    font-size: 2rem;
    background-color: rgba(255, 255, 255, 0.9);
    padding: 0.5rem;
    border-radius: 5px;
    position: absolute;
    right: 1rem;
    top: -3.5rem;
    cursor: pointer;
}

.live-search-close:hover {
    background-color: rgb(202, 202, 202);
}

.search-overlay-enter {
    opacity: 0;
    transform: scale(1.3);
}

.search-overlay-enter-active {
    opacity: 1;
    transform: scale(1);
    transition: 0.33s visibility ease-in-out, 0.33s opacity ease-in-out,
        0.33s transform ease-in-out;
}

.search-overlay-exit {
    opacity: 1;
    transform: scale(1);
}

.search-overlay-exit-active {
    opacity: 0;
    transform: scale(1.3);
    transition: 0.33s visibility ease-in-out, 0.33s opacity ease-in-out,
        0.33s transform ease-in-out;
}

.pointer {
    cursor: pointer;
}

.live-search-results {
    opacity: 0;
    height: 0;
}

.live-search-results--visible {
    opacity: 1;
    height: auto;
}

.search-item:hover {
    background-color: rgb(202, 202, 202);
    border-radius: 5px;
}

.cover {
    object-fit: cover;
    object-position: -120px 0;
}

.shine {
    background: #f6f7f8;
    background-image: linear-gradient(
        to right,
        #f6f7f8 0%,
        #edeef1 20%,
        #f6f7f8 40%,
        #f6f7f8 100%
    );
    background-repeat: no-repeat;
    display: inline-block;
    position: relative;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-name: placeholderShimmer;
    animation-timing-function: linear;
}

.loading-line {
    height: 20px;
    margin-top: 10px;
    width: 300px;
}

.loading-image {
    height: 197px;
    margin-top: 10px;
    width: 350px;
}

.loading-image-sub {
    width: 250px;
    height: 100px;
}

.loading-image-sub-text {
    height: 200px;
}

.loading-time {
    height: 10px;
    width: 100px;
}

.loading-description {
}

@keyframes placeholderShimmer {
    0% {
        background-position: -468px 0;
    }

    100% {
        background-position: 468px 0;
    }
}
